<template>
  <div  class="callbackTop" @click="callbackTop" v-show="show">
    <div class="triangle"></div>
  </div>
</template>

<script>
export default {
  name: 'callbackTop',
  data () {
    return {
      show: false
    }
  },
  methods: {
    callbackTop () {
      document.documentElement.scrollTop = 0
    },
    divShow () {
      let top = document.documentElement.scrollTop
      if (top > 300) {
        this.show = true
      } else {
        this.show = false
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.divShow)
  },
  // 解绑全局事件 切换页面 页面隐藏执行钩子 移除全局事件
  beforeDestroy () {
    window.removeEventListener('scroll', this.divShow)
  }
}
</script>

<style lang="stylus" scoped>
  .callbackTop
    position fixed
    z-index 999
    bottom .5rem
    right .5rem
    width .8rem
    height .7rem
    border-radius .1rem
    background #ffffff
    box-shadow 1px 2px 2px #ccc
    .triangle
      margin .08rem auto 0
      width 0
      height 0
      border-style solid
      border-color transparent transparent #ccc transparent
      border-width .2rem
</style>



