<template>
  <div>
    <div class="recommend">
      <div class="sellPointTitle">
        <i class="sellPointIcon"></i>
        {{recommend.recommendTitle}}
      </div>
      <ul class="recommendWarpper">
        <router-link
          class="recommendContent"
          v-for="item of recommend.recommendContent"
          :key="item.id"
          tag="li"
          :to="'/detail/' + item.id"
        >
          <div class="recommendImg">
            <img :src="item.recommendContentUrl" alt="">
          </div>
          <h3>{{item.recommendContentTil}}</h3>
          <p><em>￥</em>{{item.recommendContentPrice}}<i>起</i></p>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailRecommend',
  props: {
    recommend: {
      type: Object,
      detfault () {
        return {
          recommend: {}
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'

  .recommend
    width 100%
    .sellPointTitle
      position relative
      padding .3rem .2rem .3rem .8rem
      background #fff
      color #333
      font-size .3rem
      line-height 1
      .sellPointIcon
        position absolute
        left .2rem
        top .3rem
        width .28rem
        height .28rem
        background-image: url(https://pic.c-ctrip.com/VacationH5Pic/group_travel/index/tour_icon_all02.png)
        background-repeat: no-repeat
        background-size: 250px auto
        background-position -1.44rem 0
    .recommendWarpper
      width 100%
      padding 0 .2rem
      box-sizing border-box
      overflow hidden
      .recommendContent
        float left
        width 49%
        margin-right 2%
        margin-bottom .4rem
        .recommendImg
          width 100%
          height 0
          padding-bottom 60%
          overflow hidden
          border-radius .08rem
          img
            width 100%
            display block
        h3
          margin-bottom .1rem
          font-size .28rem
          font-weight 400
          line-height .34rem
          height .68rem
          color #222222
          margin-top .16rem
          display -webkit-box
          -webkit-box-orient vertical
          -webkit-line-clamp 2
          overflow hidden
          // ellipsis()
        p
          color #ff6913
          font-size .36rem
          line-height 1
          em
            font-size .24rem
            font-style normal
          i
            font-size .24rem
            margin-left .04rem
            color #999
      .recommendContent:nth-child(2n)
        margin-right 0
</style>



