<template>
  <div>
    <div class="title">热销推荐</div>
    <ul>
      <!-- 解决一元素边框 引入文件 加上class -->
      <!-- to绑定js表达式 使用router-link标签 tag使用li标签渲染 传递id给路由-->
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail/' + item.id"
      >
        <img class="item-img" :src="item.imgUrl" alt="">
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-btn">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: {
      type: Array,
      default () {
        return {
          list: []
        }
      }
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  // 使用之后不显示... 在父元素上添加 min-width 0
  .title
    margin-top .2rem
    line-height .8rem
    background #eee
    text-indent .2rem
  .item
    overflow hidden
    display flex
    height 1.9rem
    .item-img
      width 1.5rem
      height 1.7rem
      padding .1rem
      border-radius .16rem
    .item-info
      flex 1
      padding .1rem
      min-width 0
      .item-title
        line-height .54rem
        font-size .32rem
        ellipsis()
      .item-desc
        line-height .4rem
        color #ccc
        ellipsis()
      .item-btn
        line-height .44rem
        margin-top .16rem
        background-color #ff9300
        padding 0 .2rem
        border-radius .06rem
        color #fff
</style>



