<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <swiper :options="swiperOptions">
        <!-- slides -->
        <swiper-slide v-for="item in bannerImg" :key="item.id">
          <img
            class="banner-img"
            :src="item.ImgUrl"
            alt=""
          />
        </swiper-slide>
        <!-- Optional controls -->
        <!-- <div class="swiper-pagination"  slot="pagination"></div> -->
      </swiper>
      <div class="banner-info">
        <div class="banner-title">
          {{fromName}}
        </div>
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe634;</span>
          7
        </div>
      </div>
    </div>
    <common-animate>
      <common-gallery
        :imgs="galleryImg"
        v-show="showGallery"
        @close="handleCloseGallery"
      >
      </common-gallery>
    </common-animate>
  </div>
</template>

<script>
import CommonGallery from 'common/gallery/Gallery'
import CommonAnimate from 'common/commonAnimate'

export default {
  name: 'DetailBanner',
  props: {
    fromName: {
      type: String,
      default () {
        return {
          fromName: ''
        }
      }
    },
    bannerImg: {
      type: Array,
      default () {
        return {
          bannerImg: []
        }
      }
    },
    galleryImg: {
      type: Array,
      default () {
        return {
          galleryImg: []
        }
      }
    }
  },
  data () {
    return {
      showGallery: false,
      imgs: [],
      swiperOptions: {
        autoplay: 5000,
        observer: true,
        observeParents: true
      }
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallery = true
    },
    handleCloseGallery () {
      this.showGallery = false
    }
  },
  components: {
    CommonGallery,
    CommonAnimate
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position relative
    overflow hidden
    height 0
    padding-bottom 56%
    .banner-img
      width 100%
    .banner-info
      display flex
      z-index 2
      position absolute
      left 0
      right 0
      bottom 0
      line-height .6rem
      color #fff
      background-image linear-gradient(top, rgba(0,0,0,0),rgba(0,0,0,.8))
      .banner-title
        flex 1
        font-size .32rem
        padding 0 .2rem
      .banner-number
        height .32rem
        line-height .32rem
        margin-top .14rem
        padding 0 .4rem
        border-radius .2rem
        font-size .24rem
        background rgba(0,0,0,.8)
        .banner-icon
          font-size .24rem
</style>



