<template>
    <div class="header">
        <div class="header-left">
            <div class="iconfont back-icon">&#xe624;</div>
        </div>
        <div class="header-input">
            <span class="iconfont">&#xe632;</span>
            输入城市/景点/游玩主题
        </div>
        <router-link to="/City">
            <div class="header-right">
                <!-- 使用vuex中的计算属性 getters -->
                <!-- {{this.doubleCity}} -->
                {{this.city}}
                <span class="iconfont arrow-icon">&#xe62d;</span>
            </div>
        </router-link>
    </div>
</template>

<script>
// import { mapGetters, mapState } from 'vuex'
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
    // 使用vuex中的计算属性 getters
    // ...mapGetters(['doubleCity'])
  }
}
</script>

<style lang="stylus" scoped>
    // 引入样式变量
    @import '~styles/varibles.styl'

    .header
        display flex
        line-height $headerHeight
        background $bgColor
        color #fff
        .header-left
            width .64rem
            float left
            .back-icon
                text-align center
                font-size .4rem
        .header-input
            flex 1
            height .64rem
            line-height .64rem
            margin-top .12rem
            margin-left .2rem
            padding-left .2rem
            background #fff
            border-radius .1rem
            color #ccc
        .header-right
            min-width 1.04rem
            padding 0 .1rem
            float right
            text-align center
            color #fff
            .arrow-icon
                margin-left -.04rem
                font-size .24rem
</style>



