<template>
  <div>
    <detail-banner
      :bannerImg="bannerImg"
      :galleryImg="galleryImg"
      :fromName="fromName"
    >
    </detail-banner>
    <detail-header></detail-header>
    <detail-list :detailInfo="detailInfo"></detail-list>
    <detail-recommend :recommend="recommend"></detail-recommend>
    <!--footer 引入组件不显示 与元素标签冲突 解决 改个名字 显示 -->
    <common-footer></common-footer>
    <callback-top></callback-top>
  </div>
</template>

<script>
import DetailBanner from './components/banner'
import DetailHeader from './components/header'
import DetailList from './components/list'
import DetailRecommend from './components/recommend'
import axios from 'axios'
import CommonFooter from 'common/footer'
import CallbackTop from 'common/callbackTop'

export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailList,
    DetailRecommend,
    CommonFooter,
    CallbackTop
  },
  data () {
    return {
      fromName: '',
      bannerImg: [],
      galleryImg: [],
      detailInfo: {},
      recommend: {}
    }
  },
  // 发送带参数的ajax请求 拼接带参数的请求 ->换做对象
  methods: {
    getDetailInfo () {
      // axios.get('/api/detail.json?id=' + this.$route.params.id)
      axios.get('/api/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        let data = res.data
        this.bannerImg = data.bannerImg
        this.galleryImg = data.galleryImg
        this.fromName = data.fromName
        this.detailInfo = data.detailInfo
        this.recommend = data.recommend
      }
    }
  },
  mounted () {
    this.getDetailInfo()
  }
}
</script>

<style lang="stylus" scoped>
  .content
    height 50rem
</style>



