<template>
  <ul class="list">
    <li
      class="item"
      v-for="item of letters"
      :key="item"
      :ref="item"
      @click="handleBtn"
      @touchstart="handleStart"
      @touchmove="handleMove"
      @touchend="handleEnd"
    >
      {{item}}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'CityAlphabet',
  props: {
    cities: {
      type: Object,
      default () {
        return {
          cities: ''
        }
      }
    }
  },
  data () {
    return {
      touchStart: false,
      startY: 0,
      timer: null
    }
  },
  // 初始cities数组为空 Ajax传入数据才有数据 所以在修改后绑定事件
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  computed: {
    letters () {
      const letters = []
      for (let i in this.cities) {
        letters.push(i)
      }
      return letters
    }
  },
  methods: {
    handleBtn (e) {
      this.$emit('change', e.target.innerText)
    },
    handleStart () {
      this.touchStart = true
    },
    handleMove (e) {
      // 当在滑动时 计算A到顶部距离 滑动当前距离减去顶部固定距离 除以元素的高度 得到滑动到几个元素
      // change值元素名给list组件切换效果 上面点击一样
      // 函数节流 当滑动非常频繁时 判断滑动时间 清除前一个短时间滑动
      if (this.touchStart) {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          let touchY = e.touches[0].clientY - 79
          let index = Math.floor((touchY - this.startY) / 20)
          if (index >= 0 && index < this.letters.length) {
            this.$emit('change', this.letters[index])
          }
        }, 15)
      }
    },
    handleEnd () {
      this.touchStart = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .list
    display flex
    flex-direction column
    justify-content center
    position absolute
    top 1.58rem
    right 0
    bottom 0
    width .4rem
    .item
      line-height .4rem
      text-align center
      color $bgColor
      user-select none
</style>



