<template>
  <transition>
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'commonAnimate'
}
</script>

<style lang="stylus" scoped>
  .v-enter, .v-leave-to
    opacity 0
  .v-enter-to, .v-leave
    opacity 1
  .v-enter-active, .v-leave-active
    transition opacity 1s
</style>



