import Vue from 'vue'
import Router from 'vue-router'
// 异步加载组件
import Home from '@/pages/Home/Home'
import City from '@/pages/City/City'
import Detail from '@/pages/Detail/Detail'

Vue.use(Router)
// redirect: '/Home' 重定向为Home alias: 'index.html' 别名
// path: '/Home',
// name: 'Home',
// component: Home,
// children: [
//   {
//     path: '/city'
//   }
// ]
// 首页默认跳转Home
// {
//   path: '/',
//   redirect: '/Home'
// }
// 异步加载组件 使用import方式异步加载组件
export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    // component: () => import('@/pages/Home/Home')
    component: Home
  },
  {
    path: '/City',
    name: 'City',
    component: City
  },
  {
    // 绑定动态路由
    path: '/detail/:id',
    name: 'Detail',
    component: Detail
  }],
  // 切换页面滑动最顶部 不设置位置出错 返回记得滑动位置
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



