<template>
  <div id="app">
    <!-- 路由被加载过一次后放入内存中 下一次直接内存加载-->
    <!-- 不重新请求详情页解决方法 exclude="Detail" 这个组件不会被缓存 -->
    <keep-alive exclude="Detail">
      <router-view/>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>

</style>



