<template>
  <div>
    <!-- 组件递归 -->
    <!-- <div
      class="item"
      v-for="(item, index) of list"
      :key="index"
    >
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-if="item.children" class="item-children">
        <detail-list :list="item.children"></detail-list>
      </div>
    </div> -->
    <div class="header-title">
      <img class="header-BgImg" :src="detailInfo.priceImg" alt=""/>
      <div class="header-wrapper">
        <span class="header-price"><span>￥</span>{{detailInfo.price}}<em>/人起</em></span>
        <span class="header-priceSave">{{detailInfo.priceSave}}分</span>
        <span class="header-people">{{detailInfo.people}}人出游</span>
      </div>
    </div>
    <div class="content">
      <div class="content-title">{{detailInfo.sightName}}</div>
      <div class="content-info">{{detailInfo.sightInfo}}</div>
      <div class="content-list">
        <i
          class="border"
          v-for="(item, index) of detailInfo.signtTag"
          :key="index"
        >
          {{item}}
        </i>
      </div>
    </div>
    <div class="sellPoint">
      <div class="sellPointTitle">
        <i class="sellPointIcon"></i>
        {{detailInfo.sellPointTitle}}
      </div>
      <ul class="sellPointWrapper">
        <li
          class="sellPointText"
          v-for="(item, index) in detailInfo.sellPoint"
          :key="index"
        >
          <h3>{{item.sellPointName}}</h3>
          <div>{{item.sellPointInfo}}</div>
        </li>
      </ul>
    </div>
    <div class="cost">
      <div class="sellPointTitle">
        <i class="sellPointIcon"></i>
        {{detailInfo.cost}}
      </div>
      <div
        class="costDescription"
        v-for="(item, index) of detailInfo.costDescription"
        :key="index"
      >
        <h3 class="costTitle">{{item.costTitle}}</h3>
        <table class="costInfo">
          <tr
            v-for="(content, index) of item.costInfo"
            :key="index"
          >
            <th class="costInfoName">{{content.costInfoName}}</th>
            <td class="costInfoContent">{{content.costInfoContent}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    // list: {
    //   type: Array,
    //   default () {
    //     return {
    //       list: []
    //     }
    //   }
    // }
    detailInfo: {
      type: Object,
      default () {
        return {
          detailInfo: {}
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  // .item-title
  //   line-height .8rem
  //   font-size .32rem
  //   padding 0 .2rem
  // .item-title-icon
  //   position relative
  //   top .06rem
  //   left .06rem
  //   display inline-block
  //   width .36rem
  //   height .36rem
  //   background url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
  //   margin-right .1rem
  //   background-size .4rem 3rem
  // .item-children
  //   padding 0 .2rem
  // line-height 1 行高根据字体大小设置 设为相同
  .border::before
    border-color rgba(102,102,102,.6)!important
  .header-title
    position relative
    z-index 2
    .header-BgImg
      display block
      width 100%
      z-index 1
    .header-wrapper
      position absolute
      top 10%
      left 0
      span
        padding .2rem .1rem 0 .1rem
        display inline-block
        font-size .24rem
        color #fff
      .header-price
        font-size .5rem
        position relative
        padding-right .2rem
        margin-right .2rem
        color #fff
        span
          font-size .24rem
        em
          font-weight 400
          font-size .24rem
          color #fff
          margin-left .04rem
      .header-priceSave
        color #fff
        font-size .28rem
        line-height .36rem
        border-radius 1rem
        padding 0 .1rem
        vertical-align -.02rem
        line-height 1
        background rgba(34,34,34,.2)
      .header-people
        color: #fff;
        vertical-align: -1px;
  .content
    padding .3rem .2rem
    background #ffffff
    margin-bottom .16rem
    .content-title
      margin-bottom .04rem
      font-weight 700
      font-size .32rem
      line-height .36rem
      color #333
    .content-info
      font-size .26rem
      color #222
      line-height .36rem
    .content-list
      line-height .32rem
      margin-top .1rem
      height .44rem
      overflow hidden
      position relative
      padding-right 1.32rem
      i
        font-size .22rem
        display inline-block
        line-height .32rem
        height .32rem
        color #666
        background #ffffff
        vertical-align top
        padding .02rem .06rem 0
        position relative
        margin .1rem .1rem 0 0
        border-radius .08rem
  .sellPoint
    padding .2rem 0
    width 100%
    background #eee
    box-sizing border-box
    .sellPointTitle
      position relative
      padding .3rem .2rem .3rem .8rem
      background #fff
      color #333
      font-size .3rem
      line-height 1
      .sellPointIcon
        position absolute
        left .2rem
        top .3rem
        width .22rem
        height .3rem
        background-image: url(https://pic.c-ctrip.com/VacationH5Pic/group_travel/index/tour_hotel.png)
        background-repeat: no-repeat
        background-size: 100px auto
        background-position 0 -.16rem
    .sellPointWrapper
      padding .1rem .2rem .3rem
      background #fff
      .sellPointText
        width 100%
        h3
          display inline-block
          margin-bottom .2rem
          line-height 1
          font-size .24rem
          color #ffffff
          padding 0.02rem .08rem
          border-radius .16rem 0
          background-image linear-gradient(90deg, #ff9913 0, #ffc92b 100%)
        div
          font-size .28rem
          color #222
          padding-bottom .3rem
          padding-left .18rem
        div:before
          content ''
          background #666
          float left
          margin .16rem 0 0 -.18rem
          height .08rem
          width .08rem
          border-radius 50%
  .cost
    width 100%
    .sellPointTitle
      position relative
      padding .3rem .2rem .3rem .8rem
      background #fff
      color #333
      font-size .3rem
      line-height 1
      .sellPointIcon
        position absolute
        left .2rem
        top .31rem
        width .28rem
        height .28rem
        background-image: url(https://pic.c-ctrip.com/VacationH5Pic/group_travel/index/tour_icon_all02.png)
        background-repeat: no-repeat
        background-size: 250px auto
        background-position -114px 0
    .costDescription
      width 100%
      padding 0 .2rem
      box-sizing border-box
      .costTitle
        font-size .3rem
        color #222
        line-height 1
        position relative
        padding-left .2rem
      .costTitle:before
        content ''
        position absolute
        width .06rem
        height .24rem
        border-radius .04rem
        background #0088ff
        top .02rem
        left .02rem
      .costInfo
        width 100%
        margin .24rem 0 0 0
        margin-bottom .3rem
        th
          width .8rem
          background #efeff4
          text-align center
          font-size .26rem
          color #222
          padding .2rem .2rem
          position relative
          font-weight 400
        // bottom线
        th:after,td:after
          position absolute
          left 0
          top 0
          content ''
          width 200%
          height 200%
          border 1px solid #e0e0e0
          border-width 0 0 1px 0
          transform scale(.5)
          transform-origin 0 0
          box-sizing border-box
        td
          background #fafafa
          font-size .26rem
          color #666
          padding .2rem
          position relative
          line-height 1.5
</style>



