import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'
import mutations from './mutations'

// 创建Vuex区域
Vue.use(Vuex)

// 创建仓库
export default new Vuex.Store({
  state,
  // 接收传过来的数据 上下文 传过来的城市--执行方法名
  // actions: {
  //   changeCity (ctx, city) {
  //     ctx.commit('changeCity', city)
  //   }
  // },
  // 仓库的名称 外部传过来的数据--改变数据
  mutations,
  getters: {
    doubleCity (state) {
      return state.city + '示'
    }
  }
})



